import React, {Component} from "react";
import {connect} from "react-redux";

import MultiCartExpandingSectionComponent from "../shared/MultiCartExpandingSectionComponent";
import MultiCartCollapsedItemComponent from "../mobile/MultiCartCollapsedItemComponent";
import {
    removeFromCart
} from "../../../actions/cart";
import ModalService from "eshop/flux/utils/OraModalService";
import {FloatingBox} from "./BoxFragments";
import MultiCartRemovePopup from "../mobile/MultiCartRemovePopup";
import MultiCartAssignmentInstalmentComponent from "../mobile/MultiCartAssignmentInstalmentComponent";
import TransferMainProductComponent from "./TransferMainProductComponent";

class TransferComponent extends Component  {

    constructor(props){
        super(props);
        this.state = {
            selectedSection: null
        }
    }

    openRemovePopup() {
        const {entry} = this.props;
        ModalService.open('remove-from-cart-modal-' + entry.bundleNo, {
            bundleNo: entry.bundleNo,
            offerIndex: entry.bundleNo
        });
    }

    registerRemovePopup(entry) {
        ModalService.add(() => <MultiCartRemovePopup id={'remove-from-cart-modal-' + entry.bundleNo}
                                                         bundleNo={entry.bundleNo}
                                                         title={this.props.sectionConf.deleteEntryPopupTitle}
                                                         content={this.props.sectionConf.deleteEntryPopupContent}
                                                         decline={this.props.sectionConf.declineDeleteEntryPopupButtonLabel}
                                                         confirm={this.props.sectionConf.confirmDeleteEntryPopupButtonLabel}
                                                         onClickRemove={this.props.removeFromCart.bind(this, null, entry.bundleNo)}
                                                         textRepresentation={this.props.sectionConf.sectionTitle}/>);
    }

    componentDidMount() {
        this.registerRemovePopup(this.props.entry);
    }

    render(){
        const title = this.props.sectionConf.sectionTitle;
        const idx = "transfer_section"+this.props.idx;
        const trigger = 'ost-' + this.props.entry.bundleNo;

        return (
            <div>
                <MultiCartExpandingSectionComponent title={title} key={idx} entry={this.props.entry}
                                                    trigger={trigger}
                                                    conf={this.props.sectionConf}
                                                    onRemoveClicked={() => this.openRemovePopup(this.props.entry)}>
                    <div>
                        <FloatingBox key={idx}>
                            <ProductSectionWrapper key={idx} entry={this.props.entry} sectionTitle={""}>
                                <TransferMainProductComponent key={idx }
                                                         {...this.props}
                                                         showNetPrices={this.props.showNetPrices}
                                />
                            </ProductSectionWrapper>
                        </FloatingBox>
                        <FloatingBox key={"instalment" + idx}>
                            <MultiCartAssignmentInstalmentComponent {...this.props}/>
                        </FloatingBox>
                    </div>
                    <MultiCartCollapsedItemComponent key={"collapsed" + idx}/>
                </MultiCartExpandingSectionComponent>
            </div>

        )
    }
}

const ProductSectionWrapper = (props) => {
    return (
        <div className="u-box-shadow">
            {props.sectionTitle &&
            <h3 className="h3 u-small-hide u-padding-all-l u-no-spacing">{props.sectionTitle}</h3>}
            {props.children}
        </div>
    );
};

const mapDispatchToProps = (dispatch) => ({
    removeFromCart: (data, id) => dispatch(removeFromCart(data, id))
});

export default connect(null,mapDispatchToProps)(TransferComponent);
